import "./App.css";

import React from "react";
import * as Constants from "./constants.js";

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}
const images = importAll(require.context('./tft_data', true, /\.(png|jpe?g|svg)$/));

console.log(images["champions/TFT5_Teemo.png"])

class Composition extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: null,
    };
  }

  render() {
    return (
      <>
        <div>
          <button
            onClick={() =>
              this.setState({
                value: Constants.MetaComps[Math.floor(Math.random() * Constants.MetaComps.length)],
              })
            }
          >
            Give Comp
          </button>
        </div>
        <div>
          <p>{this.state.value}</p>
        </div>
      </>
    );
  }
}

class ChampionSelector extends React.Component {
  render() {
    return (
      <img src={images["champions/TFT5_Teemo.png"]} alt="Champion"/> 
    );
  }
}

function App() {
  return (
    <div>
      <h1>TFT Comps</h1>
      <Composition />
      <ChampionSelector />
    </div>
  );
}

export default App;
